.todoListMain {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
   
}
.todoListMain > header  input,button {
    padding: 10px;
    font-size: 1.2em;
    border: none;
    box-shadow: 2px 2px 5px grey ;
    outline: none;
    width: 300px;
    border-radius: 5px;
}
.todoListMain > header button {
    margin: 10px;
    width: 70px;
    background-color: 	SteelBlue;
    color: white;
    
}

.todoListMain > header button:hover {
    background-color: DarkBlue;
    cursor: pointer;
}
.todoListMain .theList {
    margin: 10px 0px;
    padding-left: 0px;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.todoListMain .theList li{
    list-style: none;
    background-color: rgba(255,255,255,0.5);
    padding: 15px;
    margin-bottom: 15px;
    width: 90%;
    border-radius: 5px;

    transition: background-color .2s ease-in;
}
.todoListMain .theList li:hover{
    background-color: SteelBlue;
    color: white;
}