body , html{
    /* padding: 50px; */
    background-color: #66ccff;
    margin: 0px;
    width: 100%;
    height: 100%;
}
article {
    width: 80%;
    height: 80%;
    margin: auto;
    margin-top: 80px;
}
article, article>header{
    display: flex;
    flex-direction: column;
    align-items: center;
    

}
#container {
    display: flex;
    justify-content: center;
    margin-top: 80px;
}

article header p , article footer{
        margin: 10px;
        font-size: 2em;
        font-family: 'Times New Roman', Times, serif;
        font-style: italic;
        color: 		rgb(23, 23, 53);

}
article header p:first-child{
    font-size: 2.4em;
}
article footer{
    font-size: 1em;
    margin-top: 30px;
}